.container {
  max-width: 1140px;
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
}

.container-fluid {
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;
}

.hero-block {
  width: -webkit-fill-available;
  height: 100vh;
  background-size: cover !important;
  background-attachment: fixed !important;
  position: relative;
  overflow: hidden;
}
.hero-block .heading-container {
  padding-top: 45vh;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes slideUp {
  0% {
    opacity: 0;
    bottom: -25px;
  }
  100% {
    opacity: 1;
    bottom: 25px;
  }
}
@keyframes slideDown {
  0% {
    top: -51px;
  }
  100% {
    top: 10px;
  }
}
.hero-block h1 {
  position: absolute;
  margin: 0;
  left: 0;
  right: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  width: fit-content;
  line-height: normal;
  animation: 5s ease-in 0s 1 fadeIn;
  transition: ease-in-out 0.3s;
  pointer-events: none;
  letter-spacing: 20px;
  margin-left: 10px;
}
.hero-block h1.heading-mobile {
  display: none;
}
@media (max-width: 1300px) {
  .hero-block h1.heading {
    transform: scale(0.8) translate(-63%, -65%);
  }
}
@media (max-width: 1024px) {
  .hero-block h1.heading {
    transform: scale(0.6) translate(-83.4%, -85%);
  }
}
@media (max-width: 730px) {
  .hero-block h1.heading {
    display: none;
  }
  .hero-block h1.heading-mobile {
    display: block;
    color: #aa0000;
  }
}
.hero-block h1.heading-overlay {
  pointer-events: none;
}
.hero-block .item-container {
  width: 40px;
  text-align: right;
}
.hero-block .social-links {
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  width: fit-content;
  bottom: 25px;
  display: flex;
  gap: 15px;
  text-align: center;
  animation: 1s ease-in-out 0s 1 slideUp;
}
.hero-block .social-links .dashicons {
  font-size: 40px;
  min-width: fit-content;
  transition: ease-in-out 0.3s;
  transition-property: font-size color;
}
@media (max-width: 730px) {
  .hero-block .social-links .dashicons {
    font-size: 30px;
  }
}
.hero-block .social-links .dashicons.social-icon {
  cursor: pointer;
}
.hero-block .social-links .dashicons.social-icon:hover {
  color: #aa0000;
  margin-top: -3px;
}
.hero-block .social-links.scrolled {
  right: 0;
  padding: 0;
  position: fixed;
  bottom: unset;
  animation: 0.25s ease-in 0s 1 slideDown;
  top: 10px;
  z-index: 100;
  gap: 6px;
}
@media (min-width: 1280px) {
  .hero-block .social-links.scrolled {
    width: 1280px;
    justify-content: right;
  }
}
.hero-block .social-links.scrolled .dashicons {
  font-size: 33px;
  transition: all ease-in-out 0.3s;
}
.hero-block .social-links.scrolled .dashicons.social-icon:hover {
  margin-top: 0px;
}
@media (max-width: 1280px) {
  .hero-block .social-links.scrolled {
    left: unset;
    right: 20px;
  }
}
.hero-block .social-links a {
  color: white;
}

.music-block .music-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-bottom: 250px;
  gap: 20px;
}
@media (max-width: 767px) {
  .music-block .music-wrap {
    padding-bottom: 126px;
  }
}
.music-block .music-wrap h3 {
  font-weight: 400;
}
.music-block .music-wrap .col {
  width: calc(33% - 10px);
}
.music-block .music-wrap .col iframe {
  height: 352px;
  box-shadow: 3px 3px 15px rgba(0, 0, 0, 0.2);
}
@media (max-width: 1024px) {
  .music-block .music-wrap .col {
    width: calc(50% - 10px);
  }
}
@media (max-width: 768px) {
  .music-block .music-wrap .col {
    width: 100%;
  }
}

.music-slider-block {
  padding-bottom: 250px;
}
@media (max-width: 767px) {
  .music-slider-block {
    padding-bottom: 126px;
  }
}
.music-slider-block .music__slider {
  background: #ad0000;
  margin: 0 auto;
  padding: 40px 26px;
  box-shadow: 15px 15px 0 white;
  margin-right: 15px;
}
.music-slider-block .music__slider h3 {
  font-weight: 400;
}
